import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_TOP_BACK = mu.marginTopBack(false);
const MARGIN_BOTTOM = mu.marginBottom();

import Team from '../../components/team';

const initialState = {
	isLoading: false,
};

@observer
class Teams extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadTeams() {
		this.setState({ isLoading: true });
		const { teamStore } = this.props.stores;
		await teamStore.load(undefined, false, { addData: ['workoutSummary'] });
		this.setState({ isLoading: false });
	}

	componentDidMount() {
		this.loadTeams();
	}

	render() {
		const { isLoading } = this.state;
		const { teamStore, userStore } = this.props.stores;
		const { teams } = teamStore;
		const { user } = userStore;
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row'>
					<div class='col-12'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<h5><Text id='teams.all-teams' fields={{ total: teams.length }}>Teamene dine - {teams.length} stk</Text></h5>
						</div>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 clearfix mb-2'>
							<a class='btn btn-sm btn-primary float-right'
								href='/teams/edit/new'
							>
								<i class='fas fa-plus' /> <Text id='teams.add-new'>Legg til team</Text>
							</a>
						</div>
						{teams && teams.map(team => (
							<Team stores={this.props.stores} team={team} />
						))}
					</div>
				</div>
			</div>
		);
	}
}

export default Teams;
